* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  background-color: #f4f4f4;
  color: #333;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.header {
  background-color: #333;
  color: #fff;
  padding: 10px 0;
}

.header .navbar {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .logo {
  font-size: 1.5rem;
  text-decoration: none;
  color: #fff;
}

.nav-links {
  list-style: none;
  display: flex;
}

.nav-links li {
  margin-right: 20px;
}

.nav-links li a {
  text-decoration: none;
  color: #fff;
  transition: color 0.3s ease;
}

.nav-links li a:hover {
  color: #f0f0f0;
}

.main {
  padding: 20px 0;
}

.projects {
  background-color: #fff;
  padding: 40px 0;
}

.project-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.project-item {
  padding: 20px;
  background-color: #f0f0f0;
  border-radius: 5px;
}

.project-item h3 {
  margin-bottom: 10px;
  font-size: 1.2rem;
}

.project-item p {
  font-size: 1rem;
  color: #666;
}

.footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 20px 0;
  bottom: 0;
  width: 100%;
}

.about {
  margin: 500px 0px;
}

.projects {
  margin: 500px 0px;
}

.contact {
  margin: 500px 0x;
}